* {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

body {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.container {
  width: 800px;
  height: 600px;
  border: 1px solid #000;
  align-items: center;
  margin-top: 100px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.slide {
  width: 100%;
  height: 100%;
  transition: all .5s ease-in-out;
  position: absolute;
}

.back, .next {
  width: 100px;
  height: 50px;
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: rgba(0, 0, 0, .5);
  font-size: 30px;
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
}

.back {
  transform: translateX(-300%);
}

.next {
  transform: translateX(300%);
}

